/*  */
@each $ta in $text-align {
    .ta#{$ta}{
        text-align: map-get($text-align-base, $ta);
    }
}

@each $ta in $text-align {
    @each $rwd in $rwd-reakpoints {
        @media (min-width:map-get($rwd-reakpoint-base, $rwd)) {
            .ta-#{$rwd}-#{$ta}{
                text-align: map-get($text-align-base, $ta);
            }
        }
    }
}


/* font-size  */
//  class .fz- 
@for $i from 1 through 150{
    .fz-#{$i}{
        font-size: $fz-root * $i;
    }
}

@each $rwd in $rwd-reakpoints {
    @for $i from 1 through 150{
        @media (min-width:map-get($rwd-reakpoint-base, $rwd)) {
            .fz-#{$rwd}-#{$i}{
                font-size: $fz-root * $i;
            }
        }
    }
}

//  BS5  $font-sizes ( h1~h6  RWD )  class  .fs- 
@each $h, $fs in $font-sizes {
    @each $rwd in $rwd-reakpoints {
        @media (min-width:map-get($rwd-reakpoint-base, $rwd)) {
            .fs-#{$rwd}-#{$h}{
                font-size: $fs !important;
            }
        }
    }
}


/* line-height  */
@for $i from 2 through 150{
    .lh-#{$i}{
        line-height: $fz-root * $i;
    }
}

@each $rwd in $rwd-reakpoints {
    @for $i from 2 through 150{
        @media (min-width:map-get($rwd-reakpoint-base, $rwd)) {
            .lh-#{$rwd}-#{$i}{
                line-height: $fz-root * $i;
            }
        }
    }
}

/* font-style  */
.italic{
    font-style: italic;
}


/* font-weight  */
@for $i from 1 through 9{
    .fw-#{$i*100}{
        font-weight: $i * 100;
    }
}

@each $rwd in $rwd-reakpoints {
    @for $i from 1 through 9{
        @media (min-width:map-get($rwd-reakpoint-base, $rwd)) {
            .fw-#{$rwd}-#{$i*100}{
                font-weight: $i * 100;
            }
        }
    }
}
